    line-height: 17px;
    color: #FFFFFF;
    margin-bottom: 1rem;
    display: block;
}

.btn-store {
    padding: 17px 21px !important;
}

.float-reponsive {
    float: right;
}

.toggle {
    position: relative;
    float: right;
}

.toggle:before {
    content: '';
    position: absolute;
    border-bottom: 2px solid #fff;
    border-right: 2px solid #fff;
    width: 4px;
    height: 10px;
    z-index: 2;
    transform: rotate(45deg);
    top: 7px;
    left: 10px;
}

.toggle:after {
    content: 'Ã—';
    position: absolute;
    top: 4px;
    left: 39px;
    z-index: 2;
    line-height: 20px;
    font-size: 22px;
    color: #fff;
}

.toggle input[type="checkbox"] {
    position: absolute;
    left: 0;
    top: 0;
    z-index: 10;
    width: 100%;
    height: 100%;
    cursor: pointer;
    opacity: 0;
}

.toggle label {
    position: relative;
    display: flex;
    align-items: center;
}

.toggle label:before {
    content: '';
    /* width: 80px;
    height: 42px; */
    /* box-shadow: 0 0 1px 2px #0001; */
    width: 60px;
    height: 30px;
    background: #000;
    position: relative;
    display: inline-block;
    border-radius: 46px;
    transition: 0.2s ease-in;
    /* padding: 0 .4rem; */
}

.toggle label:after {
    content: '';
    position: absolute;
    /* width: 42px;
    height: 42px; */
    width: 15px;
    height: 15px;
    border-radius: 50%;
    left: 6px;
    /* top: 0; */
    top: 7px;
    z-index: 5;
    background: #fff;
    /* box-shadow: 0 0 5px #0002; */
    transition: 0.2s ease-in;
}

.toggle input[type="checkbox"]:hover+label:after {
    box-shadow: 0 2px 15px 0 #0002, 0 3px 8px 0 #0001;
}

.toggle input[type="checkbox"]:checked+label:before {
    transition: 0.1s 0.2s ease-in;
    background: #000;
}

.toggle input[type="checkbox"]:checked+label:after {
    left: 38px;
}

.dark-theme {
    font-family: 'Inter';
    font-style: normal;
    font-weight: 400;
    font-size: 16px;
    line-height: 18px;
    color: #FFFFFF;
    margin-left: 15px;
}

@media only screen and (max-width: 959px) {
    .toggle {
        margin-top: 2rem;
    }

    .toggle,
    .float-reponsive {
        float: none;
    }

    .foter-contents-width {
        max-width: fit-content;
    }
}
@media only screen and (max-width: 800px){
    .padding-responsive{
        padding: 80px;
    }
}
@media only screen and (max-width: 425px){
    .padding-responsive{
        padding: 20px;
    }
}