body {
  /* background: radial-gradient(48.48% 57.94% at 51.52% 37.6%, #061C31 0%, #061523 30.2%, #02060B 73.49%, #000000 82.27%); */
  background-size: 100%;
  background: url("./assets/images/back-ground-image.png");
  height: 100%;
  background-size: cover;
}

.App {
  /* background-color: #000; */
  min-height: 100vh;
  font-family: 'Inter';
  font-style: normal;
  font-weight: 400;
  font-size: 18px;
  line-height: 22px;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}


.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

@media (max-width: 920px) {
  footer .MuiGrid-container .footer-img {
    margin: 0 auto;
    display: block !important;
    margin-bottom: 25px;
  }
}

.logotext {
  font-family: 'Inter', sans-serif;
  font-style: normal;
  font-weight: 400;
  font-size: 24px;
  line-height: 136px;
  color: #FFFFFF;
  margin-left: 18px;
}

 header,
.drawerContainer {
  background-color: inherit !important;
  display: flex;
}

.drawer-mobile .MuiDrawer-paper {
  background-color: #160040 !important;
}

.mobile-logo{
  margin: 0 auto;
  display: block;
  margin-top: 38px;
}

.button-link {
  text-decoration: none;
}

@media only screen and (max-width: 959px){
  .logotext{
      display: none;
  }
}