@import url('https://fonts.googleapis.com/css2?family=Ephesis&family=Inter:wght@200;300&family=Island+Moments&family=Italianno&family=Open+Sans:wght@300;400;600&family=Roboto:wght@300;400;500;700&family=Twinkle+Star&display=swap');

body {
    font-family: 'Inter', sans-serif !important;
    font-style: normal;
}

.MuiContainer-root {
    padding: 0 !important;
    margin: 0;
}

/* .home-bg {
    background-color: #160040;
    max-width: 100% !important;
} */

/* section one start */
.form-bgpng {
    display: block;
    position: fixed;
    width: 100%;
    height: 50vh;
    top: 30%;
    z-index: -1;
    background: url("../../assets/images/homepageimg/form\ bg.png") top repeat-x;
}

.title-section {
    font-family: 'Inter', sans-serif !important;
    font-weight: 500 !important;
    font-size: 32px !important;
    line-height: 39px !important;
    color: #FFFFFF;
}


.testiominals-title {
    font-family: 'Inter', sans-serif !important;
    font-weight: 600 !important;
    font-size: 32px !important;
    line-height: 39px !important;
    color: #FFFFFF;
}

.card-form {
    background: #1F1D36 !important;
    box-shadow: 0px 4px 24px rgba(0, 0, 0, 0.1) !important;
    border-radius: 8px !important;
    padding: 3rem;
    max-width: 450px !important;
    margin: 0 auto !important;
}

.form-btn {
    background: #7C3AED !important;
    border-radius: 4px !important;
    font-weight: 400 !important;
    font-size: 16px !important;
    line-height: 19px !important;
    color: #FFFFFF !important;
    text-transform: none !important;
    padding: .7rem 1.8rem !important;
    min-width: 86px !important;
    margin: .5rem .5rem !important;
    box-shadow: 0 !important;
}

.btn-buy-sell-active {
    background: transparent !important;
    border: 1px solid #FFFFFF !important;
}

.label-name {
    margin-bottom: 0;
    font-weight: 400;
    font-size: 14px;
    line-height: 16px;
    color: #FFFFFF;
    position: relative;
    top: 15px;
}

.top-zero {
    top: 0;
}

.customeinput-and-customeimg .MuiCardMedia-media {
    width: auto !important;
    position: relative;
    z-index: 1;
    left: 15px;
    right: 0;
    top: 41px;
}

.customeinput-and-customeimg input {
    /* position: relative; */
    border: 1px solid rgba(255, 255, 255, 0.5) !important;
    border-radius: 4px !important;
    min-width: -webkit-fill-available;
    padding: 1.2rem 0 1.4rem 3rem;
    background: transparent;
    color: #FFFFFF;
    font-size: 16px;
}

.customeinput-and-customeimg input::placeholder {
    font-weight: 400;
    font-size: 16px;
    line-height: 19px;
    color: #FFFFFF;
}



/* section one over */


/* section two start */
.section-two {
    margin: 3.5rem auto 5rem;
    padding: 0 2rem;
}

.bg-leftside{
    background-image: url("../../assets/images/homepageimg/borderleft.png");
    background-repeat: no-repeat;
    padding: 2rem 0.5rem .2rem .8rem;
    max-width: 340px;
}

.custome-card-sectiontwo {
    border: 1px solid #15BFFD;
    background: rgba(255, 255, 255, 0.05) !important;
    border-radius: 5.30268px !important;
    /* transform: matrix(-1, 0, 0, 1, 0, 0) !important; */
    max-width: 340px;
    padding: 0.5rem 0;
    top: -21px;
    position: relative;
}

.custome-card-sectiontwo .card-header-content .MuiCardMedia-media {
    width: auto !important;
    margin-right: 15px;
    object-fit: contain !important;
}

.sectiontwocard-image-content {
    font-weight: 500 !important;
    font-size: 24px !important;
    color: #FFFFFF;
    margin: 0 auto;
}

.sectiontwocard-image-contentsub {
    font-weight: 400 !important;
    font-size: 16px !important;
    color: #767676;
}

.graphac-numeric {
    font-weight: 500 !important;
    font-size: 24px !important;
    line-height: 29px !important;
    color: #FFFFFF;
}
.custome-card-sectiontwo .card-body-content .MuiCardMedia-media {
    width: auto !important;
    object-fit: contain !important;
}


/* section testiominals start */

/* .testiominals-title {
    font-family: 'Inter', sans-serif !important;
    font-weight: 500 !important;
    font-size: 32px !important;
    line-height: 39px !important;
    color: #FFFFFF;
} */

.testiominals-paragraph {
    font-weight: 400 !important;
    font-size: 16px !important;
    line-height: 19px !important;
    color: #FFFFFF;
    display: block;
    padding-bottom: 4rem;
}

.pb-two {
    padding-bottom: 2rem;
}

.cards-media .MuiCardMedia-media {
    width: auto !important;
    object-fit: contain !important;
    margin-right: 20px;
}

.cards-main {
    border: 1px solid #15BFFD !important;
    background: rgba(255, 255, 255, 0.05) !important;
    border-radius: 8px !important;
    margin: 0 5px;
}

.card-image-content {
    font-weight: 400 !important;
    font-size: 16px !important;
    line-height: 20px !important;
    color: #FFFFFF;
}

.read-more {
    color: #9C37FD !important;
    font-weight: 500 !important;
}

.mt-ten {
    margin-top: 8rem !important;
}

.create-btn {
    background: #7C3AED !important;
    border-radius: 4px !important;
    font-weight: 400 !important;
    font-size: 16px !important;
    line-height: 19px !important;
    color: #FFFFFF !important;
    text-transform: none !important;
    padding: .7rem 1rem !important;
    min-width: 209px !important;
    margin: 0 auto !important;
    display: block !important;
}

.bg-testiominals-leftright{
    padding: 2rem;
}


/* section testiominals over */

/* reponsive meadia queries start */
@media only screen and (max-width: 899px) {
    .mt-ten {
        margin-top: 2rem !important;
    }
    .bg-leftside{
        margin: 0 auto;
    }
}

@media only screen and (max-width: 600px){
    .title-section, .testiominals-title {
        font-size: 20px !important;
    }
    .testiominals-paragraph{
        padding-bottom: 2rem;
    }
    
}
@media only screen and (max-width: 585px){
    .card-form{
        max-width: 300px !important;
    }
}

@media only screen and (max-width: 425px){
    .card-form{
        max-width: 280px !important;
        padding: 2rem;
    }
}
/* reponsive meadia queries over */
